import React, { Component } from "react";
import { Link } from "react-router-dom";

import injectSheet from "react-jss";

const styles = {
  menuBackground: {
    background: "#3f51b5",
    height: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column"
  },
  buttonGame: {
    borderRadius: "10px",
    outline: "0",
    border: "none",
    transform: "scale(2.5)",
    color: "#3f51b5",
    background: "white"
  },
  header: {
    fontSize: "5rem",
    color: "white"
  }
};

class StartPage extends Component {
  render() {
    const { classes } = this.props;
    return (
      <div>
        <div className={classes.menuBackground}>
          <h1 className={classes.header}>C</h1>
          <Link to="/game">
            <button className={classes.buttonGame}></button>
          </Link>
        </div>
      </div>
    );
  }
}

export default injectSheet(styles)(StartPage);



