import React, { Component } from "react";

import { sudoku } from "../../libraries/sudoku";

import { Link } from "react-router-dom";

import injectSheet from "react-jss";
import classNames from "classnames";

import { decorate, observable, action, computed } from "mobx";
import { observer } from "mobx-react";

import magicWand from "../../assets/magicWand.png";

const styles = {
  table: {
    display: "table",
    margin: "auto",
    fontSize: "1.2rem",
    textAlign: "center",
    background: "white",
    borderCollapse: "collapse",
    margin: "10px auto",
    position: "relative",
    top: "-278px",
    borderRadius: "20px;",
    border: "none",
    borderSpacing: "0",
    boxShadow: "0px 4px 48px 1px rgba(0,0,0,0.75)"
  },
  "@media (max-width: 576px)": {
    table: {
      width: "92%"
    }
  },
  "@media (min-width: 578px)": {
    table: {
      width: "80%"
    }
  },
  "@media (min-width: 1023px)": {
    table: {
      width: "50%"
    }
  },
  "@media (min-width: 1280px)": {
    table: {
      width: "28%"
    }
  },
  "@media (min-width: 1921px)": {
    table: {
      width: "30%"
    }
  },
  td: {
    width: "11.11%",
    position: "relative",
    "&:after": {
      content: "''",
      marginTop: "100%",
      display: "block"
    },
    borderRight: "solid 1px #e0e0e0",
    color: "#673ab7"
  },
  tdLast: {
    width: "11.11%",
    position: "relative",
    "&:after": {
      content: "''",
      marginTop: "100%",
      display: "block"
    },
    color: "#673ab7"
  },
  content: {
    position: "absolute",
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },

  tdBorderRight: {
    width: "11.11%",
    position: "relative",
    "&:after": {
      content: "''",
      marginTop: "100%",
      display: "block"
    },
    color: "#673ab7",
    borderRight: "solid 3px #ada8a8"
  },
  trBorderBottom: {
    borderBottom: "solid 3px #ada8a8"
  },
  trBorderBottomAll: {
    borderBottom: "solid 1px #e0e0e0"
  },
  tdBorderTest: {
    width: "11.11%",
    position: "relative",
    "&:after": {
      content: "''",
      marginTop: "100%",
      display: "block"
    },
    background: "#673ab712"
  },
  tdClick: {
    color: "black",
    background: "#a394bc"
  },
  tdClickFalse: {
    background: "#f44336",
    color: "white"
  },
  tdClickTrue: {
    outline: "none",
    background: "#4caf50",
    color: "white"
  },
  buttonNum: {
    position: "relative",
    display: "block",
    margin: "5px 5px",
    padding: 0,
    overflow: "hidden",
    borderWidth: 0,
    outline: "none",
    borderRadius: "10px",
    boxShadow: "0 1px 4px rgba(0, 0, 0, .6)",
    backgroundColor: "#673ab7",
    color: "#eaeaea",
    transition: "backgroundColor .3s",
    top: "-280px",
    width: "50px",
    height: "40px",
    fontSize: "2rem"
  },
  header: {
    background: "#673ab7",
    height: "320px"
  },
  headerDiv: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },
  count: {
    borderRadius: "10px",
    padding: "4px 3px",
    color: "white",
    marginLeft: "5px",
    marginRight: "5px",
    fontSize: "17px"
  },
  error: {
    borderRadius: "10px",
    background: "#eaeaea",
    padding: "4px 22px",
    color: "#673ab7",
    fontSize: "16px"
  },
  hint: {
    borderRadius: "10px",
    background: "#eaeaea",
    padding: "4px 6px",
    color: "#673ab7",
    fontSize: "16px"
  },
  magicWand: {
    background: `url(${magicWand})`,
    backgroundPosition: "center",
    backgroundSize: "cover"
  }
};

class TableSudoku extends Component {
  ii = undefined;
  jj = undefined;
  count = undefined;
  countError = 0;
  countHint = 0;

  str = sudoku.board_string_to_grid(sudoku.generate("medium"));
  strSolve = sudoku.board_string_to_grid(
    sudoku.solve(sudoku.board_grid_to_string(this.str))
  );
  clickTd = [];

  componentWillMount() {
    this.StartTimeNow();
  }

  getZZ = (i, j) => {
    if (
      i >= 0 &&
      i < 3 &&
      j >= 0 &&
      j < 3 &&
      this.ii >= 0 &&
      this.ii < 3 &&
      this.jj >= 0 &&
      this.jj < 3
    ) {
      return true;
    } else if (
      i >= 0 &&
      i < 3 &&
      j >= 3 &&
      j < 6 &&
      this.ii >= 0 &&
      this.ii < 3 &&
      this.jj >= 3 &&
      this.jj < 6
    ) {
      return true;
    } else if (
      i >= 0 &&
      i < 3 &&
      j >= 6 &&
      j < 9 &&
      this.ii >= 0 &&
      this.ii < 3 &&
      this.jj >= 6 &&
      this.jj < 9
    ) {
      return true;
    }

    if (
      i >= 3 &&
      i < 6 &&
      j >= 0 &&
      j < 3 &&
      this.ii >= 3 &&
      this.ii < 6 &&
      this.jj >= 0 &&
      this.jj < 3
    ) {
      return true;
    } else if (
      i >= 3 &&
      i < 6 &&
      j >= 3 &&
      j < 6 &&
      this.ii >= 3 &&
      this.ii < 6 &&
      this.jj >= 3 &&
      this.jj < 6
    ) {
      return true;
    } else if (
      i >= 3 &&
      i < 6 &&
      j >= 6 &&
      j < 9 &&
      this.ii >= 3 &&
      this.ii < 6 &&
      this.jj >= 6 &&
      this.jj < 9
    ) {
      return true;
    }

    if (
      i >= 6 &&
      i < 9 &&
      j >= 0 &&
      j < 3 &&
      this.ii >= 6 &&
      this.ii < 9 &&
      this.jj >= 0 &&
      this.jj < 3
    ) {
      return true;
    } else if (
      i >= 6 &&
      i < 9 &&
      j >= 3 &&
      j < 6 &&
      this.ii >= 6 &&
      this.ii < 9 &&
      this.jj >= 3 &&
      this.jj < 6
    ) {
      return true;
    } else if (
      i >= 6 &&
      i < 9 &&
      j >= 6 &&
      j < 9 &&
      this.ii >= 6 &&
      this.ii < 9 &&
      this.jj >= 6 &&
      this.jj < 9
    ) {
      return true;
    }
  };

  setSudoku = () => {
    this.str.clear();
    this.str.replace(sudoku.board_string_to_grid(sudoku.generate("medium")));
  };
  StartTimeNow() {
    var base = 60;
    var clocktimer, dateObj, dh, dm, ds, ms;
    var readout = "";
    var h = 1,
      m = 1,
      tm = 1,
      s = 0,
      ts = 0,
      ms = 0,
      init = 0;

    var StartTIME = () => {
      var cdateObj = new Date();
      var dateObj = new Date();
      var t = cdateObj.getTime();
      if (t > 999) {
        s++;
      }
      if (s >= m * base) {
        ts = 0;
        m++;
      } else {
        ts = parseInt(ms / 100 + s);
        if (ts >= base) {
          ts = ts - (m - 1) * base;
        }
      }
      if (m > h * base) {
        tm = 1;
        h++;
      } else {
        tm = parseInt(ms / 100 + m);
        if (tm >= base) {
          tm = tm - (h - 1) * base;
        }
      }
      ms = Math.round(t / 10);
      if (ms > 99) {
        ms = 0;
      }
      if (ms == 0) {
        ms = "00";
      }
      if (ms > 0 && ms <= 9) {
        ms = "0" + ms;
      }
      if (ts > 0) {
        ds = ts;
        if (ts < 10) {
          ds = "0" + ts;
        }
      } else {
        ds = "00";
      }
      dm = tm - 1;
      if (dm > 0) {
        if (dm < 10) {
          dm = "0" + dm;
        }
      } else {
        dm = "00";
      }
      dh = h - 1;
      if (dh > 0) {
        if (dh < 10) {
          dh = "0" + dh;
        }
      } else {
        dh = "00";
      }
      this.count = dm + ":" + ds;
      clocktimer = setTimeout(StartTIME, 1000);
    };

    StartTIME();
  }

  indexClick = (i, j) => () => {
    let a = [];

    a.push(i, j);

    this.ii = a[0];
    this.jj = a[1];
  };

  get checkGame() {
    return sudoku.board_grid_to_string(this.str) ==
      sudoku.board_grid_to_string(this.strSolve)
      ? ""
      : "";
  }

  getHint = () => {
    console.log(this.ii);
    if (this.ii != undefined) {
      if (
        this.str[this.ii][this.jj] == "." ||
        this.str[this.ii][this.jj] != this.strSolve[this.ii][this.jj]
      ) {
        this.str[this.ii][this.jj] = this.strSolve[this.ii][this.jj];
        this.countHint++;
        this.clickTd[this.ii][this.jj] = true;
      }
    }
  };

  inFalse = () => {
    let clickTd = [];
    for (let i = 0; i < 9; i++) {
      clickTd[i] = [];
      for (let j = 0; j < 9; j++) {
        clickTd[i][j] = true;
      }
    }
    return clickTd;
  };
  clickTd = this.inFalse();

  setNumberToStr = i => () => {
    if (this.ii == undefined || this.jj == undefined) {
      return false;
    }
    if (this.str[this.ii][this.jj] == "." || !this.clickTd[this.ii][this.jj]) {
      this.str[this.ii][this.jj] = i;
      if (this.str[this.ii][this.jj] !== this.strSolve[this.ii][this.jj]) {
        this.clickTd[this.ii][this.jj] = false;
      }
    }
    if (this.str[this.ii][this.jj] == this.strSolve[this.ii][this.jj]) {
      this.clickTd[this.ii][this.jj] = true;
    }

    if (this.str[this.ii][this.jj] != this.strSolve[this.ii][this.jj]) {
      this.countError++;
    }
  };

  get getStats() {
    if ((this.ii || this.jj) == undefined) {
      return false;
    }
    return this.str[this.ii][this.jj] == this.strSolve[this.ii][this.jj];
  }

  createTable() {
    const { classes } = this.props;
    let table = [];

    for (let i = 0; i < 9; i++) {
      let children = [];
      for (let j = 0; j < 9; j++) {
        children.push(
          <td
            key={i + j}
            className={classNames(
              `${
                this.getZZ(i, j) || this.ii == i || this.jj == j
                  ? classes.tdBorderTest
                  : ""
              } ${(j + 1) % 3 == 0 && j != 8 ? classes.tdBorderRight : ""} ${
                (j + 1) % 3 !== 0 && j != 8 ? classes.td : classes.tdLast
              } ${i == this.ii && j == this.jj ? classes.tdClick : ""} ${
                this.getStats && i == this.ii && j == this.jj
                  ? classes.tdClickTrue
                  : ""
              } ${this.clickTd[i][j] == false ? classes.tdClickFalse : ""}`
            )}
            onClick={this.indexClick(i, j)}
          >
            {
              <div className={classNames(classes.content, ` `)}>
                {
                  <span className={classNames()}>
                    {this.str[i][j] == "." ? "" : this.str[i][j]}
                  </span>
                }
              </div>
            }
          </td>
        );
      }
      table.push(
        <tr
          key={i}
          className={
            (i + 1) % 3 == 0 && i != 8
              ? classes.trBorderBottom
              : `${i != 8 ? classes.trBorderBottomAll : ""}`
          }
        >
          {children}
        </tr>
      );
    }
    return table;
  }

  createButton() {
    const { classes } = this.props;
    let buttonArrTop = [];
    let buttonArrBottom = [];

    for (let i = 1; i < 10; i++) {
      i < 6
        ? buttonArrTop.push(
            <button
              onClick={this.setNumberToStr(i)}
              key={i}
              className={classes.buttonNum}
            >
              {i}
            </button>
          )
        : buttonArrBottom.push(
            <button
              onClick={this.setNumberToStr(i)}
              key={i}
              className={classes.buttonNum}
            >
              {i}
            </button>
          );
    }

    return (
      <React.Fragment>
        <div style={{ display: "flex", justifyContent: "center" }}>
          {buttonArrTop}
        </div>
        <div style={{ display: "flex", justifyContent: "center" }}>
          {buttonArrBottom}
          <button
            onClick={this.getHint}
            className={classNames(classes.buttonNum, classes.magicWand)}
          />
        </div>
      </React.Fragment>
    );
  }

  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <div className={classes.header}>
          <div className={classes.headerDiv}>
            <h1 className={classes.error}>: {this.countError}</h1>

            <h1 className={classes.count}>{this.count}</h1>

            <h1 className={classes.hint}>: {this.countHint}</h1>
          </div>
        </div>
        <table className={classes.table}>
          <tbody style={{ padding: "10px" }}>{this.createTable()}</tbody>
        </table>
        {this.createButton()}
      </React.Fragment>
    );
  }
}

decorate(TableSudoku, {
  ii: observable,
  jj: observable,
  str: observable,
  clickTd: observable,
  count: observable,
  countError: observable,
  countHint: observable,
  getHint: action,
  StartTimeNow: action,
  setNumberToStr: action,
  indexClick: action,
  checkGame: computed
});

export default injectSheet(styles)(observer(TableSudoku));



